@use 'main' as *;

.header {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 40px;
    padding: 2rem 0;
    border-bottom: 1px solid var(--dark-graish-blue);
    box-shadow: 0px 4px 2px -3px var(--dark-graish-blue);
    position: relative;

    &__h1 {
        padding-bottom: 0.6rem;
    }

    &__ul {
        list-style-type: none;
        display: flex;
        gap: 10px;
    }

    &__li {
        display: inline-block;
    }

    &__anchor {
        text-decoration: none;
        padding: 0.7rem;
        font-size: 18px;
        transition: outline 150ms ease-in-out;

        &:any-link {
            color: var(--dark-graish-blue);
        }

        &:hover, &:focus {
            outline: 3px solid var(--dark-graish-blue);
            border-radius: 15px;
            text-shadow: 1px 1px 2px var(--primary-orange);
            color: var(--primary-orange);
            font-weight: 500;
        }
    }

    &__cart {
        margin-left: auto;
        position: relative;
        cursor: pointer;
        padding: 1rem;
        background-color: inherit;
        border: none;

        &--icon {
            width: 22px;
            height: 20px;
        }
           

        & .notification {
            background-color: var(--primary-orange);
            color: var(--white);
            position: absolute;
            top: 3px;
            right: 2px;
            width: 18px;
            height: 18px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 10px;
            padding: 11px;
        }
    }

    &__avatar {
        width: 50px;
        height: 50px;
        align-self: center;
    }

    .cart-info {
        visibility: hidden;
        position: absolute;
        top: 120px;
        right: -100px;
        background-color: var(--white);
        box-shadow: 0px 1px 5px -2px var(--dark-graish-blue);
        border-radius: 15px;
        padding-block: 15px;
        width: 450px;
        height: 250px;
        display: flex;
        flex-direction: column;
        justify-content: start;
        gap: 12px;

        &__h3 {
            padding: 0 0 25px 15px;
            border-bottom: 0.5px solid var(--dark-graish-blue);
            justify-self: flex-start;
        }

        &__init {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;

            &--text {
                font-size: 1.2rem;
                font-weight: bold;
                color: var(--dark-graish-blue);
            }
        }

        &__wrapper {
            display: none;
            flex-direction: column;
            height: 100%;
        }

        &__main {
            display: flex;
            padding: 15px;
            align-items: center;
            justify-content: space-around;
        }

        picture {
            line-height: 0;
            border-radius: 15px;
        }

        &__img {
            width: 65px;
            height: 65px;
            border-radius: 15px;
        }

        &__container {
            display: flex;
            flex-direction: column;
            gap: 10px;

            > p:first-child {
                font-weight: 600;
                color: var(--dark-graish-blue);
                font-size: 18px;
            }

            > p:last-child {
               color: var(--dark-graish-blue);
               font-size: 17px;
            }
        }
        
        &__button {
            padding: 10px;
            border: none;
            background-color: inherit;
            cursor: pointer;
        }

        &__checkout {
            background-color: var(--primary-orange);
            width: 90%;
            height: 60px;
            align-self: center;
            color: var(--white);
            font-size: 18px;
            border-radius: 15px;
            letter-spacing: 1.5px;

            &:hover, &:focus {
                background-color: lighten($primary-color, 15%);
            }
        }

        &__price-sum {
            font-weight: bold;
        }
    }
}